header {
    height: 130vh;
    padding-top: 4rem;
    padding-bottom: 6rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}
.header_socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.me {
    background: linear-gradient( var(--color-primary),transparent);
    
    width: 22rem;
    height: 30rem;
    position:absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}
.scroll_down{
    position: absolute;
    right: 29.5rem;
    bottom: -5rem;
    transform: rotate(0deg);
    font-weight: 300;
    font-size: 0.9rem;
    
}
/* for medium devices*/
@media screen and (max-width:1024px){
    header {
        height: 68vh;
    }


}

/* for small devices */
@media screen and (max-width:600px){
    header{
        height: 100vh;
    }
    .header_socials,
    .scroll_down{
        display: none;
    }
}